<template>
    <div class="defaultLayout">
        <transition name="slide-fade">
            <NavContent/>
        </transition>
        <button v-show="isShowMainbtn">
            <router-link to="/SignIn">
                <span>开始预约</span>
                <svg xmlns="http://www.w3.org/2000/svg" version="1.1" viewbox="0 0 200 80" v-on:mouseleave="LeaveBtn">
                    <path d="m1 15 L15 1 L185 1 L199 15 L199 65 L185 79 L15 79 L1 65 z" fill="transparent" ref="btnpath"/>
                </svg>
            </router-link>
        </button>
        <router-view></router-view>
    </div>
</template>

<script>
import NavContent from '../components/NavContent'

export default {
    name:'MainContent',
    components:{NavContent},
    data() {
        return {
            isShowMainbtn:1
        }
    },
    methods: {
        LeaveBtn(){
            // 鼠标离开按钮效果
            this.$refs.btnpath.setAttribute("class","leavesvg");
        },
    },
    watch: {
        // 监听路由变化触发过渡动画
        '$route' (){
            this.$bus.$emit("pagetrans",1)
            // 判断按钮是否隐藏
            if(this.$route.path != '/'){
                this.isShowMainbtn = 0
            }else{
                setTimeout(() => {
                    this.isShowMainbtn = 1
                }, 1000);
            }
        },
        // 即刻判断按钮是否隐藏
        '$route.path' : {
            immediate:true,
            handler(){
                if(this.$route.path != '/'){
                    this.isShowMainbtn = 0
                }
            }
        }


    },
}

</script>

<style lang="less" scoped>
.defaultLayout{
    width: 100vw;
    height: 100vh;
    position: relative;
    @media (1200px <= width) {
        background-image: url(@/assets/background.png);
    }
    @media (992px <=width< 1200px) {
        background-image: url(@/assets/background1200.png);
    }
    @media(768px <=width< 992px) {
        background-image: url(@/assets/background992.png);
    }
    @media(480px <=width< 768px) {
        background-image: url(@/assets/background768.png);
    }
    @media(width < 480px) {
        background-image: url(@/assets/background480.png);
    }
    background-repeat: no-repeat;
}
button{
    width: 200px;
    height: 80px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    a{
        display: flex;
        width: inherit;
        height: inherit;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        span{
            color: #005024;
            font-size: xx-large;
            margin-top: -5px;
        }
        svg{
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: .5;
            transition: opacity .5s ease;
            path{
                stroke-width: 1;
                stroke: #005024;
                // 总长520
                stroke-dasharray: 280,240;
                stroke-dashoffset: -450;
                transition: all .5s ease;
            }
        }
    }
}
button:hover a{
    svg{
        opacity: 1;
        path{
            stroke: #163a26;
            animation: entersvg .5s ease;
        }
    }
}
@keyframes entersvg {
    from{
        stroke-dashoffset: 0;
    }
    to{
        stroke-dashoffset: -450;
    }
}
.leavesvg{
    animation: leavesvg .4s ease;
}
@keyframes leavesvg {
    from{
        stroke-dashoffset: 0;
    }
    to{
        stroke-dashoffset: -450;
    }
}

</style>