import VueRouter from "vue-router";
import SignIn from "../pages/SignIn";
import GoReservation from "../pages/GoReservation"
import MyAppointment from "../pages/MyAppointment"

// 创建暴露路由器
const router = new VueRouter({
    routes:[
        {
            path:'/SignIn',
            component: SignIn,
            beforeEnter: (to,from,next) => {
                if(localStorage.getItem("SigninToken") != 1){
                    // 没有登陆
                    next()
                }else{
                    // 已登陆
                    next({ path: '/GoReservation' })
                }
            }
        },
        {
            path:'/GoReservation',
            component: GoReservation,
            // props: true,
            beforeEnter: (to,from,next) => {
                // 判断是否登陆
                if(localStorage.getItem("SigninToken") != 1){
                    // 没有登陆则隐藏预约按钮
                    to.params.see = false
                    next()
                }else{
                    // 已登陆则显示预约按钮
                    to.params.see = true
                    next()
                }
                
            }
        },
        {
            path:'/MyAppointment',
            component: MyAppointment
        }

    ]
})


export default router