<template>
  <transition name="slide-fade" appear>
    <div class="userPanel" id="mobileDevice">
      <el-form
        :label-position="labelPosition"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="myform"
      >
        <el-form-item
          label="姓名"
          prop="name"
          for="myname"
          v-show="isShowRegister"
        >
          <el-input v-model="ruleForm.name" id="myname"></el-input>
        </el-form-item>

        <el-form-item
          label="性别"
          prop="gender"
          for="gender"
          v-show="isShowRegister"
        >
          <el-select
            v-model="ruleForm.gender"
            placeholder="请选择性别"
            id="gender"
          >
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="出生日期" required v-show="isShowRegister">
          <el-col :span="11">
            <el-form-item prop="date" class="datechoose">
              <el-date-picker
                type="date"
                placeholder="选择日期"
                v-model="ruleForm.date"
                style="width: 100%"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>

        <el-form-item
          label="手机号"
          prop="phone"
          for="phone"
          v-show="isShowRegister"
        >
          <el-input v-model.number="ruleForm.phone" id="phone"></el-input>
        </el-form-item>

        <el-form-item label="身份证" prop="citizenid" for="citizenid">
          <el-input v-model="ruleForm.citizenid" id="citizenid"></el-input>
        </el-form-item>

        <el-form-item label="密码" prop="pass" for="pwd">
          <el-input
            type="password"
            v-model="ruleForm.pass"
            autocomplete="off"
            id="pwd"
          ></el-input>
        </el-form-item>

        <div
          class="btn-holder"
          v-show="isShowRegister"
          :class="{ failtoSignup: SignupResult }"
        >
          <button
            class="btn btn-1 hover-filled-slide-left"
            @click.prevent="ShowReg"
          >
            <span>已有账号?</span>
          </button>
          <button
            class="btn btn-2 hover-slide-right"
            @click.prevent="submitSignUpForm"
          >
            <span>下一步</span>
          </button>
        </div>

        <div
          class="btn-holder"
          v-show="!isShowRegister"
          :class="{ failtoSignin: SigninResult }"
        >
          <button class="btn btn-2 hover-slide-left" @click.prevent="ShowReg">
            <span>返回注册</span>
          </button>
          <button
            class="btn btn-2 hover-slide-right"
            @click.prevent="submitSignInForm"
          >
            <span>登陆</span>
          </button>
        </div>
      </el-form>
    </div>
  </transition>
</template>

<script>
import axios from "axios";
export default {
  name: "SignIn",
  data() {
    return {
      labelPosition: "left",
      isShowRegister: true,
      SigninResult: false,
      SignupResult: false,
      ruleForm: {
        name: "",
        gender: "",
        date: "",
        phone: "",
        citizenid: "",
        pass: "",
      },
      rules: {
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max: 4, message: "请输入正确的名字", trigger: "blur" },
        ],
        gender: [{ required: true, message: "请选择性别", trigger: "change" }],
        date: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change",
          },
        ],
        phone: [
          { required: true, message: "请填写手机号码", trigger: "blur" },
          { type: "number", message: "手机号码必须为数字值", trigger: "blur" },
        ],
        citizenid: [
          {
            required: true,
            message: "身份证作为登陆账号不能为空",
            trigger: "blur",
          },
          {
            type: "string",
            len: 18,
            message: "请输入正确的身份证号",
            trigger: "blur",
          },
        ],
        pass: [
          { required: true, message: "请填写密码", trigger: "blur" },
          { len: 8, message: "输入8位数密码", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    ShowReg() {
      this.isShowRegister = !this.isShowRegister;
      this.SigninResult = false;
      this.SignupResult = false;
    },
    submitSignInForm() {
      axios
        .get(
          `http://localhost:3000/user?citizenid=${this.ruleForm.citizenid}&pass=${this.ruleForm.pass}`
        )
        .then(
          (Response) => {
            // 取消登陆失败字体
            this.SigninResult = false;
            if(Response.data.length != 0){
              // 将需要的信息存进本地 Storage 对象
              localStorage.setItem("SigninToken", 1);
              localStorage.setItem("userid", Response.data[0].id);
              localStorage.setItem("username", Response.data[0].name);
              // 进行转跳
              this.$router.push({ path: "GoReservation" });
            }else{
              this.SigninResult = true;
            }
            
          }
        );
    },
    submitSignUpForm() {
      this.$refs["ruleForm"].validate((status) => {
        if (status) {
          // 根据身份证号查询是否已经注册
          axios
            .get(
              `http://localhost:3000/user?citizenid=${this.ruleForm.citizenid}`
            )
            .then(
              // json-server查询成功与否都会返回（200）
              (Response) => {
                if (Response.data.length == 0) {
                  // 返回为空 则进行注册
                  axios.post("http://localhost:3000/user", this.ruleForm).then(
                    // 切换登陆
                    this.ShowReg()
                  );
                } else {
                  // 查询到数据则显示已注册
                  this.SignupResult = true;
                }
              }
            );
        }
      });
    },
  },
  created() {
    // 根据监听窗口变化选择表单样式
    window.addEventListener("resize", () => {
      if (window.innerWidth < 480) {
        this.labelPosition = "top";
      } else {
        this.labelPosition = "left";
      }
    });
    // 初次调用
    if (window.innerWidth < 480) {
      this.labelPosition = "top";
    }
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
};
</script>

<style lang="less" scoped>
.userPanel {
  position: fixed;
  width: 460px;
  height: auto;
  clip-path: polygon(0 0, 80% 0, 100% 18%, 100% 100%, 8% 100%, 0 95%);
  border: 5px solid #0050248f;
  top: 18%;
  right: 50%;
  margin-right: -230px;
  padding: 27px;
  background-color: transparent;
}

// 面板动画
.slide-fade-enter-active {
  animation: in 1.5s cubic-bezier(0.78, 0.1, 0.16, 0.29);
}
.slide-fade-leave-active {
  animation: out 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
@keyframes in {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes out {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(-20px);
    opacity: 0;
  }
}
// 表单按钮
.btn-holder {
  display: flex;
  justify-content: space-around;
  ::before,
  ::after {
    position: absolute;
    content: "";
  }
}
.btn {
  position: relative;
  display: inline-block;
  width: auto;
  height: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
  margin: 15px 5px 0;
  min-width: 150px;
  span {
    position: relative;
    display: inline-block;
    font-size: 17px;
    font-weight: bold;
    letter-spacing: 2px;
    top: 0;
    left: 0;
    width: 100%;
    padding: 15px 20px;
    transition: 0.3s;
  }
}

.btn-1::before {
  background-color: rgba(21, 217, 165, 0.8);
  transition: 0.3s ease-out;
}
.btn-1 span {
  color: rgb(255, 255, 255);
  border: 1px solid rgba(21, 217, 165, 0.8);
  transition: 0.2s 0.1s;
}
.btn-1 span:hover {
  color: #005024;
  transition: 0.2s 0.1s;
}
.btn.hover-filled-slide-left::before {
  top: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.btn.hover-filled-slide-left:hover::before {
  width: 0%;
}

.btn-2::before {
  background-color: #007b37;
  transition: 0.3s ease-out;
}
.btn-2 span {
  color: #005024;
  border: 1px solid #007b37;
  transition: 0.2s;
}
.btn-2 span:hover {
  color: rgb(255, 255, 255);
  transition: 0.2s 0.1s;
}

.btn.hover-slide-right::before {
  top: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 0%;
}
.btn.hover-slide-right:hover::before {
  width: 100%;
}
.btn.hover-slide-left::before {
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 0%;
}
.btn.hover-slide-left:hover::before {
  width: 100%;
}

// 注册失败状态
.failtoSignup::before {
  content: "已经注册账号,请登陆";
  color: #ff3c3c;
  position: absolute;
  top: 0;
}

// 登陆失败状态
.failtoSignin::before {
  content: "登陆失败,请检查信息";
  color: #ff3c3c;
  position: absolute;
  top: 0;
}
</style>

<style lang="less">
// 调整elemeui样式

// 移动端适配
@media screen and (width <= 480px) {
  #mobileDevice {
    width: 100%;
    padding: 8px;
    margin: 0;
    right: 0;
    .myform {
      .el-form-item {
        margin: 5px;
        margin-bottom: 15px;
        label {
          line-height: 28px;
        }
        .el-form-item__content {
          line-height: 28px;
          .el-input {
            font-size: 12px;
            input {
              height: 28px;
              line-height: 28px;
            }
          }
        }
      }
      // 取消日期框额外的下外边距
      .datechoose {
        margin-bottom: 0;
      }
      .btn-holder {
        flex-direction: column-reverse;
      }
    }
  }
}
@media screen and (height <= 750px) {
  #mobileDevice {
    .myform {
      .el-form-item {
        margin: 0 0 12px;
        .datechoose {
          margin-bottom: 0;
        }
      }
      .btn-holder {
        flex-direction: row;
        .btn {
          margin: 6xp 5px 0;
        }
      }
    }
  }
}
.myform {
  .el-form-item__label {
    padding: 0;
    font-size: 16px;
    font-weight: bold;
    color: #005024;
  }
  .el-form-item {
    margin-bottom: 25px;
  }
  .el-form-item__error {
    color: rgba(255, 60, 60, 0.676);
  }
  .el-input__inner {
    color: white;
    background-color: #0f8946b5;
    border: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 1);
  }
  .el-form-item__error {
    color: rgba(255, 60, 60, 1);
  }
  #myname {
    width: 50%;
  }
  #phone {
    width: 60%;
  }
  // 取消日期框额外的下外边距
  .datechoose {
    margin-bottom: 0;
  }
}
</style>