<template>
    <div>
        <transition name="nav-wrapper">
            <div class="navWrapper" v-show="status">
            </div>
        </transition>
        <transition name="unfold-text">
            <div class="options" v-show="status">
                <ul>
                    <li @click="hideit"><router-link to="/GoReservation"><span>出诊信息</span></router-link></li>
                    <li @click="hideit"><router-link to="/MyAppointment"><span>预约信息</span></router-link></li>
                </ul>
            </div>
        </transition>
    </div>
</template>

<script>
export default {
    name:'UnfoldNav',
    props:['status'],
    methods: {
        hideit(){
            this.$bus.$emit('turnoffnav')
        }
    },
}
</script>

<style lang="less" scoped>

.navWrapper{
    width: 100%;
    height: 150%;
    position: fixed;
    background-color: #42BA84;
    transform-origin: 0 0;
    opacity: 1;
    z-index: 1;
}
// 背景动画
.nav-wrapper-enter-active{
    transition-delay: 0.25s;
    transition: transform 0.4s ease-out;
    transform: skewY(-12deg) translateY(-100%);
}
.nav-wrapper-leave-active{
    transition: transform 0.4s cubic-bezier(.21,.58,.74,.99);
}
.nav-wrapper-enter-to,.nav-wrapper-leave-from{
    transform: skewY(-12deg) translateY(0%);
}
.nav-wrapper-leave-to{
    transform: skewY(-12deg) translateY(100%);
}

.options{
    height: 100vh;
    display: grid;
    position: relative;
    padding: 100px 50px;
    grid-template-rows: repeat(5,1fr);
    grid-template-columns: repeat(12,1fr);
    @media screen and (width<=992px){
        grid-template-columns: repeat(8,1fr);
        padding: 0 0;
    }
    @media screen and (width<=480px){
        grid-template-columns: repeat(6,1fr);
    }
    align-items: start;
    z-index: 2;
    ul{
        grid-row-start: 4;
        grid-column-end: span 3;
        grid-column-start: 2;
        @media screen and (width<=512px){
            grid-column-end: span 4;
        }
        a{
            display: inline-block;
            color: #fff;
        }
        span{
            font-size: xxx-large;
            transition: color .3s ease;
        }
        span:hover{
            color: #fff;
        }
    }
    ul:hover{
        a{
            color: rgba(255, 255, 255, 0.5);
        }
    }
}
// 文字动画
.unfold-text-enter-active{
    animation: wordenter 1s ease ;
}
.unfold-text-leave-active{
    animation: wordleave .2s ease;
}
@keyframes wordenter {
    from{
        opacity: 0;
        transform: translateY(-20px);
    }
    to{
        opacity: 1;
        transform: translateY(0);
    }
}
@keyframes wordleave {
    from{
        opacity: 1;
    }
    to{
        opacity: 0;
    }
}



</style>
