<template>
  <div class="navContent">
    <div class="nav">
      <div class="brand" @click="goHome">
        <svg class="brandicon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"  width="200" height="200"><path d="M682.666667 341.333333V42.666667H341.333333v298.666666H42.666667v341.333334h298.666666v298.666666h341.333334v-298.666666h298.666666V341.333333z" fill="#FCBEA9" p-id="2564"></path><path d="M981.333333 298.666667h-256V42.666667a42.666667 42.666667 0 0 0-42.666666-42.666667H341.333333a42.666667 42.666667 0 0 0-42.666666 42.666667v256H42.666667a42.666667 42.666667 0 0 0-42.666667 42.666666v341.333334a42.666667 42.666667 0 0 0 42.666667 42.666666h256v256a42.666667 42.666667 0 0 0 42.666666 42.666667h341.333334a42.666667 42.666667 0 0 0 42.666666-42.666667v-256h256a42.666667 42.666667 0 0 0 42.666667-42.666666V341.333333a42.666667 42.666667 0 0 0-42.666667-42.666666z m-42.666666 341.333333h-256a42.666667 42.666667 0 0 0-42.666667 42.666667v256H384v-256a42.666667 42.666667 0 0 0-42.666667-42.666667H85.333333V384h256a42.666667 42.666667 0 0 0 42.666667-42.666667V85.333333h256v256a42.666667 42.666667 0 0 0 42.666667 42.666667h256v256z" fill="#FF4C35" p-id="2565"></path></svg>
        <span :class="{switchColor:isBarActive}">省人民医院</span>
      </div>
      <ul class="navlist" :class="{hideNavOption:isBarActive}">
        <li @click="showMask">
          <router-link to="/GoReservation" active-class="hold">出诊查询<div class="underline" ><div></div></div></router-link>
        </li>
        <li @click="showMask">
          <router-link to="/MyAppointment" active-class="hold">预约信息<div class="underline" ><div></div></div></router-link>
        </li>
          <li class="SideBar" @click="switchshowbar">
              <a class="spinbtn" :class="{isBarActive}">
                <svg xmlns="http://www.w3.org/2000/svg" version="1.1" viewBox="0 0 30 30">
                  <polygon points="8,1 22,1 29,8 29,22 22,29 8,29 1,22 1,8 " />
                </svg>
              </a>
            <div class="inner" :class="{btncross:isBarActive}"></div>
          </li>
      </ul>
    </div>
    <UnfoldNav :status="isBarActive"/>
  </div>
</template>

<script>
import UnfoldNav from './UnfoldNav'

export default {
    name:'NavContent',
    data() {
      return {
        isBarActive:false,
        holdStUnderline:false,
        holdNdUnderline:false
      }
    },
    methods: {
      showMask(){
        // this.$bus.$emit('pagetrans',1)
      },
      switchshowbar(){
        this.isBarActive = !this.isBarActive
      },
      // 返回首页
      goHome(){
        // 避免导航到自己
        if(this.$router.currentRoute.path != '/'){
          this.$router.push({ path: '/' })
        }
      }
    },
    components:{
      UnfoldNav
    },
    mounted() {
      this.$bus.$on('turnoffnav',()=>{
            this.isBarActive = false
        })
    },
}
</script>

<style lang="less" scoped>
.navContent{
  position: relative;
  height: inherit;
  overflow: hidden;
}
.nav{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 10vw;
  position: fixed;
  top: 5%;
  color: #005024;
  z-index: 3;
  // background-color: lightgoldenrodyellow;
}
.brand{
  display: inherit;
  align-items: center;
  font-size: xx-large;
  font-family: 'STXingkai';
  cursor: pointer;
  svg{
    width: 60px;
    height: 60px;
  }
  span{
    margin: 5px 0 0 10px;
    letter-spacing: 2px;
  }
}
.switchColor{
  color: #fff;
}
.navlist{
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #005024;
  font-family: 'Microsoft Yahei';
  // 15F5BA
  li{
    font-size: 18px;
    margin: 0 2vw;
    transition: opacity .3s ease;
    a{
      position: relative;
      color: inherit;
    }
    .brandicon{
      width: 30px;
      height: 30px;
    }
    a:hover {
      .underline div{
        transform: translateX(0%);
      }
    }
  }
  li:not(.SideBar){
  // 窗口小于800px时，不显示-出诊信息--我的预约-
    @media(width<= 800px){
      display: none;
    }
  }
}
.underline{
  overflow: hidden;
  position: absolute;
  bottom: -8px;
  width: 100%;
  height: 1px;
  div{
    position: absolute;
    transform: translateX(-102%);
    width: 100%;
    height: 1px;
    background-color: #005024;
    transition: transform .2s cubic-bezier(0.4,0,1,1);
  }
}

//固定下划线
.hold div{
  transform: translateX(0);
}
//侧边栏按钮 
.SideBar{
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;
  .inner{
    left: 5px; 
    // 准确值被父元素flex水平居中影响,应取消后再计算  
    position: absolute;
  }
}
// 取消移动端hover样式
@media (hover:hover) {
  // 使用媒体查询，具有 hover 事件的设备才会显示 hover 样式
  .SideBar:hover {
    .inner::before{
      margin-top: -10px;
      position: absolute;
    }
    .inner::after{
      margin-top: 10px;
      position: absolute;
    }
  }
}

// 汉堡
.inner::before,
.inner::after {
  background-color: #005024;
  content: "";
  height: 2px;
  width: 26px;
  transition: all .5s cubic-bezier(0.68,-0.55,0.265,1.55);
}
.inner::before {
  margin-top: -3px;
  position: absolute;
}
.inner::after {
  margin-top: 5px;
  position: absolute;
}
//激活按钮（启用isActive类）汉堡变形为X
.btncross::before,
.btncross::after {
  transform-origin: 0 50%;
  width: 28px;
}
.btncross::before {
  margin-top: -10px;
  position: absolute;
  transform: translateX(3px) rotate(45deg) ;
}
.btncross::after {
  margin-top: 10px;
  position: absolute;
  transform: translateX(3px) rotate(-45deg);
}

// 按钮边框
.spinbtn {
  display: flex;
  width: 60px;
  height: 60px;
  cursor: pointer;
  opacity: 0;
  transition: opacity .3s ease;
  svg {
    polygon {
      stroke: #005024;
      stroke-width: 1;
      stroke-dasharray: 55,40;
      fill: none;
      transition: stroke-dashoffset .3s ease;
    }
  }
}

//激活按钮（启用isActive类）为X加边框
.isBarActive{
  opacity: 1;
  svg polygon{
    stroke-dashoffset: -15;
  }
}

// 隐藏导航栏文字选项
.hideNavOption li:not(.SideBar){
  opacity: 0;
  // 取消点击事件和设置光标的类型
  pointer-events: none;
  cursor: default;
}
</style>