<template>
  <transition name="page-transition">
    <div class="pageTransition" v-show="pageStatus"></div>
  </transition>
</template>

<script>
export default {
    name:'PageTransition',
    data() {
        return {
            pageStatus:0,
        }
    },
    methods: {

    },
    mounted() {
        this.$bus.$on('pagetrans',(isactive,ishold)=>{
            // 激活过渡动画
            this.pageStatus = isactive;
            // console.log('pagetrans----isreceive');
            //是否停在过渡动画中
            if(!ishold){
                // console.log('ishold');
                // 默认时间结束
                setTimeout(() => {
                    this.pageStatus = 0;
                }, 800);
            }
        })
    },
}
</script>

<style lang="less" scoped>
.pageTransition{
    width: 100%;
    height: 150%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(184, 211, 209);
    transform-origin: 0 0;
    opacity: 1;
    z-index: 3;
}
.page-transition-enter-active{
    transition-delay: 0.25s;
    transition: transform 0.3s ease-out;
    transform: skewY(-12deg) translateY(-100%);
}
.page-transition-leave-active{
    transition: opacity 0.3s cubic-bezier(.21,.58,.74,.99);
}
.page-transition-enter-to,.page-transition-leave-from{
    transform: skewY(-12deg) translateY(0%);
}
.page-transition-leave-to{
    opacity: 0;
}
.afteranime{
    transform: translateY(100%);
}
</style>