<template>
  <transition name="slide-fade" appear>
    <div class="reservationContent">
      <div class="selectproject">
        <div class="block selectbox" v-show="ismoblie">
          <el-cascader
            placeholder="请选择科室"
            clearable
            v-model="value"
            :options="options"
            :show-all-levels="false"
            @change="handleChange"
          ></el-cascader>
        </div>

        <div class="block selectbox" v-show="!ismoblie">
          <el-cascader
            placeholder="请选择科室"
            clearable
            v-model="value"
            :options="options"
            :show-all-levels="false"
            :props="{ expandTrigger: 'hover' }"
            @change="handleChange"
          ></el-cascader>
        </div>

        <div class="block">
          <el-date-picker
            v-model="datevalue"
            type="date"
            placeholder="请选择预约日期"
            @change="filterDoc"
            :picker-options="pickerOptions"
          >
          </el-date-picker>
        </div>
      </div>

      <el-collapse v-model="activeName" accordion>
        <el-collapse-item
          v-for="(d, index) in doctor"
          :key="index"
          :title="d.name"
          :name="index"
        >
          <img :src="d.img" alt="" />
          <div>
            <p>
              特长：{{ d.speciality }}
            </p>
            <p>
              简介：{{ d.intro }}
            </p>
          </div>
          <button @click="checkstatus" v-show="$route.params.see">预约</button>
        </el-collapse-item>
      </el-collapse>
    </div>
  </transition>
</template>

<script>
import axios from "axios";
export default {
  name: "GoReservation",
  data() {
    return {
      ismoblie: false,
      activeName: "1",
      value: [],
      datevalue: "",
      pickerOptions: {
        disabledDate(time) {
          // time.getTime()就是里面的每个日期都会来遍及比较一遍,比当前时间大,为true,禁止;false,允许
          // 8.64e7为一天的时间
          return (
            time.getTime() < Date.now() ||
            time.getTime() > Date.now() + 7 * 8.64e7
          ); 
        },
      },
      doctor: [],
      alldocs: [],
      options: [
        {
          value: "1",
          label: "内科",
          children: [
            {
              value: "1-1",
              label: "分泌内科",
            },
            {
              value: "1-2",
              label: "肾内科",
            },
            {
              value: "1-3",
              label: "呼吸与危重症状医学科",
            },
            {
              value: "1-4",
              label: "消化内科",
            },
            {
              value: "1-5",
              label: "肝胆内科",
            },
            {
              value: "1-6",
              label: "血液内科",
            },
          ],
        },
        {
          value: "2",
          label: "外科",
          children: [
            {
              value: "2-1",
              label: "肠胃外科",
            },
            {
              value: "2-2",
              label: "肝胆外科",
            },
            {
              value: "2-3",
              label: "胰腺外科",
            },
            {
              value: "2-4",
              label: "甲状腺疝外科",
            },
            {
              value: "2-5",
              label: "神经外科",
            },
            {
              value: "2-6",
              label: "骨科",
            },
            {
              value: "2-7",
              label: "整形外科",
            },
          ],
        },
        {
          value: "3",
          label: "耳喉鼻科",
          children: [
            {
              value: "3-1",
              label: "鼻科",
            },
            {
              value: "3-2",
              label: "耳科",
            },
            {
              value: "3-3",
              label: "咽喉头颈外科",
            },
          ],
        },
        {
          value: "4",
          label: "眼科",
        },
        {
          value: "5",
          label: "儿科",
          children: [
            {
              value: "5-1",
              label: "普通儿科",
            },
            {
              value: "5-2",
              label: "儿童血液科",
            },
            {
              value: "5-3",
              label: "新生儿科",
            },
          ],
        },
        {
          value: "6",
          label: "神经科",
        },
        {
          value: "7",
          label: "中医科",
        },
      ],
    };
  },
  methods: {
    // 获取医生信息
    handleChange(value) {
      if (value.length != 0) {
        // console.log(value.pop());
        axios.get(`http://localhost:3000/${value.slice(-1)}`).then((Response) => {
        // axios.get(`https://my-json-server.typicode.com/n0thing-github/doc/${value.pop()}`).then((Response) => {
          this.doctor = Response.data;
          this.alldocs = Response.data;
        });
      }
    },
    // 过滤当天未值班的医生
    filterDoc() {
      // 全部医生 筛选前备份一份,用于重新筛选
      this.doctor = this.alldocs;
      // 简化为docs
      let docs = this.doctor;
      // 选择的日期为星期几
      let dutyday = this.transweek;
      // 收集筛选出值班医生放入数组
      let todaydocs = [];
      // 循环遍历所有医生
      for (let i = 0; i < docs.length; i++) {
        // 转为字符串查看是否包含当天值班
        if (docs[i].duty.toString().includes(dutyday)) {
          todaydocs.push(docs[i]);
        }
      }
      // 赋值给模板数组重新渲染页面
      this.doctor = todaydocs;
    },
    // 检查下单状态
    checkstatus(){
      axios.get(
        `http://localhost:3000/user/${localStorage.getItem("userid")}`,
      ).then(
        (Response) => {
          // 判断是否 选择日期
          if(this.datevalue){
            // 判断是否 已经预约
            if(Response.data.reservation_name){
              this.$notify({
                title: "预约失败",
                message: "请取消之前的预约再重新预约",
                type: "error"
              })
            }else{
              // 调用下单方法
              this.pointit()
            }
          }else{
            this.$notify({
              title: "预约时间未选择",
              type: "warning",
            })
          }

          
        }
      )
    },
    // 下单
    pointit(){
        axios.patch(
          `http://localhost:3000/user/${localStorage.getItem("userid")}`,
          // `https://my-json-server.typicode.com/n0thing-github/doc/user/${localStorage.getItem("userid")}`,
          {
            reservation_time: this.datevalue.toLocaleDateString().slice(-2),
            reservation_name: this.doctor[this.activeName].name.slice(0, 3),
          }
        ).then(
          localStorage.setItem("userdutydoc",this.doctor[this.activeName].name.slice(0, 3)),
          localStorage.setItem("userpointdata",this.datevalue.toLocaleDateString()),
          this.$router.push({ path: "MyAppointment" })
        )
        this.$notify({
          title: "预约成功!!!",
          message: `已预约${this.doctor[this.activeName].name.slice(0,3)}医生,请于${this.datevalue.toLocaleDateString().slice(-2)}号就诊`,
          type: "success",
        })
    }
    
  },
  computed: {
    transweek() {
      // console.log(this.datevalue.toLocaleDateString());
      if (this.datevalue) {
        let w = this.datevalue.toString().substring(0, 3);
        switch (w) {
          case "Sun":
            return 7;
          case "Mon":
            return 1;
          case "Tue":
            return 2;
          case "Wed":
            return 3;
          case "Thu":
            return 4;
          case "Fri":
            return 5;
          default:
            return 6;
        }
      } else {
        return null;
      }
    },
  },
  mounted() {
    // console.log(localStorage.getItem("username"));
    // console.log('mounted',this.$route.params.see);
  },
};
</script>

<style lang="less" scoped>
.reservationContent {
  position: fixed;
  display: flex;
  top: 18%;
  width: 80%;
  left: 50%;
  padding: 5px;
  .selectproject {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  flex-direction: column;
  transform: translateX(-50%);
  .selectbox {
    margin-bottom: 5px;
  }
}
@media screen and (width<=520px) {
  .reservationContent {
    top: 16%;
    .selectproject {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 8px;
    }
  }
}

.slide-fade-enter-active {
  animation: in 1.5s cubic-bezier(0.78, 0.1, 0.16, 0.29);
}
.slide-fade-leave-active {
  animation: out 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
@keyframes in {
  from {
    // top: 10%;
    opacity: 0;
  }
  to {
    // top: 18%;
    opacity: 1;
  }
}
@keyframes out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
</style>

<style lang="less">
// element ui el-cascader 滚动条BUG
.el-scrollbar__wrap {
  overflow: hidden;
  margin-bottom: 0 !important;
  margin-right: 0 !important;
}
.el-cascader-menu {
  width: 100px;
  font-size: 12px;
  .el-cascader-menu__wrap {
    scrollbar-width: none;
    .el-cascader-node {
      padding: 0 0 0 5px;
      .el-icon-check:before {
        content: "";
      }
    }
  }
}
.el-collapse {
  border-color: #42ba84;
  // border-bottom-color: #42BA84;
  .el-collapse-item {
    .el-collapse-item__header {
      color: #005024;
      border-color: #42ba84;
      i::before {
        color: white;
      }
      background-color: transparent;
    }
    .is-active {
      border-bottom-color: transparent;
      font-weight: bold;
      color: lightgoldenrodyellow;
      i::before {
        color: #005024;
      }
    }
  }
  .el-collapse-item__wrap {
    background-color: transparent;
    border-bottom-color: #42ba84;
    .el-collapse-item__content {
      display: flex;
      justify-content: flex-start;
      color: #005024;
      position: relative;
      img {
        width: 100px;
        height: 100px;
        border-radius: 100%;
      }
      div {
        margin: 0 8px;
      }
      button {
        position: absolute;
        right: 0;
        bottom: 5px;
        width: 60px;
        height: 29px;
        color: white;
        line-height: 30px;
        background-color: #458c85;
        border-radius: 5px;
        cursor: pointer;
      }
    }
  }
}
@media screen and (520px<width) {
  .reservationContent {
    max-width: 800px;
    .el-collapse-item {
      .el-collapse-item__header {
        font-size: 20px;
      }
    }
    p {
      font-size: 15px;
    }
  }
}
@media screen and (width<=520px) {
  .reservationContent {
    width: 100%;
    .el-collapse-item__wrap {
      .el-collapse-item__content {
        img {
          width: 50px;
          height: 50px;
        }
      }
    }
    .el-collapse-item__header {
      font-size: 16px;
    }
  }
}
</style>