<template>
    <transition name="slide-fade" appear>
        <div class="myAppointmentPage">
            <el-descriptions class="margin-top" title="我的预约信息" :column="3" :size="size" border>
                <template slot="extra">
                    <button @click="cancel">取消预约</button>
                </template>
                <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    用户名
                </template>
                {{msg.username}}
                </el-descriptions-item>
                <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-s-custom"></i>
                    医生名称
                </template>
                {{msg.userdutydoc}}
                </el-descriptions-item>
                <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-time"></i>
                    预约日期
                </template>
                {{msg.userpointdata}}
                </el-descriptions-item>
            </el-descriptions>
        </div>
    </transition>
</template>

<script>
import axios from "axios";
export default {
    name:'MyAppointment',
    data() {
        return {
            size:'',
            msg:{
                username:'',
                userdutydoc:'',
                userpointdata:''
            }
        }
    },
    methods: {
        cancel(){
            axios.patch(`http://localhost:3000/user/${localStorage.getItem("userid")}`,{
            // axios.patch(`https://my-json-server.typicode.com/n0thing-github/doc/user/${localStorage.getItem("userid")}`,{
                reservation_name: '',
                reservation_time: '',
            }).then(
                this.$notify({
                    title: "取消预约成功",
                    type: "success",
                })
            )
            this.msg.username = null
            this.msg.userdutydoc = null
            this.msg.userpointdata = null
            localStorage.removeItem("userdutydoc")
            localStorage.removeItem("userpointdata")
        }
    },
    mounted() {
        this.msg.username = localStorage.getItem("username"),
        this.msg.userdutydoc = localStorage.getItem("userdutydoc"),
        this.msg.userpointdata = localStorage.getItem("userpointdata")
    },
    created() {
    // 根据监听窗口变化选择表单样式
    window.addEventListener("resize", () => {
      if (window.innerWidth < 480) {
        this.size = "mini";
      } else {
        this.size = "";
      }
    });
    // 初次调用
    if (window.innerWidth < 480) {
      this.labelPosition = "";
    }
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
}
</script>

<style lang="less" scoped>
.slide-fade-enter-active {
  animation: in 1.5s cubic-bezier(0.78, 0.1, 0.16, 0.29);
}
.slide-fade-leave-active {
  animation: out 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
@keyframes in {
  from {
    // top: 10%;
    opacity: 0;
  }
  to {
    // top: 18%;
    opacity: 1;
  }
}
@keyframes out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.myAppointmentPage{
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 18%;
    width: 80%;
    transform: translateX(-50%);
    left: 50%;
    padding: 5px;
    background-color: #ffffff8f;
    button{
        width: 60px;
        height: 29px;
        color: white;
        line-height: 30px;
        background-color: #458c85;
        border-radius: 5px;
        cursor: pointer;
    }
} 
@media screen and (width<480px) {
    .myAppointmentPage{
        width: 100%;
    }
}
</style>

<style lang="less">
.el-descriptions__title{
    font-size: 20px;
    color: #458c85;
}
.el-descriptions__extra{
    margin-top: 10px;
}
.el-descriptions__header{
    padding: 0 20px;
}
.el-descriptions-row{
    display: flex;
    justify-content: center;
}
@media screen and (width<678px) {
    .el-descriptions-row{
        flex-direction: column;
    }
}
</style>